@use "./../../assets/scss/palette";
@use "./../../assets/scss/typography";

.Root {
	@extend %raleway-font;
	background-color: rgba(palette.$xanadu, 1);
	min-height: 100vh;

	display: flex;
	flex-direction: column;
}

::-webkit-scrollbar {
	width: 10px;

	&-track {
		background: rgba(palette.$alabaster, 0.5);
	}

	&-thumb {
		background: palette.$ash-gray;
	}
}
